import React, { useState } from "react";
import { Link } from "gatsby";
import { DonateHart } from "./../../svg";
import "./_button.scss";

interface Props {
  text: string;
  link: string;
  color?: string;
  buttonColor?: string;
  width?: number;
  height?: number;
  hart?: boolean;
}

function Button(props: Props) {
  const { text, link, color, buttonColor, hart } = props;
  return (
    <Link
      to={`/${link}`}
      className={`button ${color ? color : ""}  ${hart ? "hart" : ""}`}
    >
      {hart ? <DonateHart color={buttonColor ? buttonColor : ""} /> : ""}
      {text}
    </Link>
  );
}

export default Button;
